* {
  box-sizing: border-box;
}
body {
  margin: 0;
  background: #e14647;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
h2 {
  background: #e14647;
  padding: 18px;
  color: #fff;
}
.App {
  padding: 30px;
  width: 80%;
  margin: 50px auto;
  background: #fff;
  box-shadow: 1px 2px 5px #eee;
}
input {
  width: 80%;
  border: 0;
  padding: 6px 10px;
  background: #e8e8e8;
}
 button {
   width: 20%;
   padding: 6px 0;
   border: 0;
   outline: none;
   margin: 0;
   font-weight: bold;
   cursor: pointer;
   background: #4dc55b;
 }
 ul {
   list-style: none;
   padding: 0;

 }
 ul li {
    margin: 15px 0;
 }
 li span {
   display: inline-block;
   width: 60%;
   color: #6a6a6a;
 }
 li >button:first-of-type {
   width: 19%;
   margin-right: 1%;
   color: #fff;
 }
 li >button:last-of-type {
   color: #fff;
 }

 ul> form {
   margin: 30px 0;
 }